import { NextRequest, NextResponse } from 'next/server'
import { createClient } from '@/lib/supabase/server'

export async function GET(request: NextRequest) {
  try {
    const supabase = await createClient()
    
    // Get the authenticated user
    const { data: { user }, error: authError } = await supabase.auth.getUser()
    
    if (authError || !user) {
      return NextResponse.json(
        { success: false, error: 'Unauthorized' },
        { status: 401 }
      )
    }

    // Get current month start date
    const now = new Date()
    const monthStart = new Date(now.getFullYear(), now.getMonth(), 1).toISOString()

    // Fetch all queries for the user
    const { data: allQueries, error: allError } = await supabase
      .from('queries')
      .select('query_type, created_at')
      .eq('user_id', user.id)

    if (allError) {
      console.error('Error fetching queries:', allError)
      return NextResponse.json(
        { success: false, error: allError.message },
        { status: 500 }
      )
    }

    // Calculate metrics
    const queries = allQueries || []
    
    const totalPlayersAnalyzed = queries.filter(
      q => q.query_type === 'Player Stats'
    ).length

    const aiPredictionsMade = queries.filter(
      q => q.query_type === 'AI Prediction - Player Prop' || 
           q.query_type === 'AI Prediction - Game Outcome'
    ).length

    const comparisonsMade = queries.filter(
      q => q.query_type === 'Player Comparison'
    ).length

    const queriesThisMonth = queries.filter(
      q => new Date(q.created_at) >= new Date(monthStart)
    ).length

    const totalQueries = queries.length

    return NextResponse.json({
      success: true,
      metrics: {
        totalPlayersAnalyzed,
        aiPredictionsMade,
        comparisonsMade,
        queriesThisMonth,
        totalQueries
      }
    }, { status: 200 })

  } catch (error) {
    console.error('Error in GET /api/dashboard-metrics:', error)
    return NextResponse.json(
      { 
        success: false, 
        error: error instanceof Error ? error.message : 'Internal server error' 
      },
      { status: 500 }
    )
  }
}
